@use 'variables' as v;

html {
   font-size: 62.5%;
   box-sizing: border-box;
}
*,
*:before,
*:after {
   box-sizing: inherit;
}

body {
   background-color: v.$gray-light;
   font-family: v.$font-main;
   font-size: 1.6rem;
}

p {
   font-size: 2rem;
}
[class$='container'],
.container {
   width: 95%;
   max-width: 1200px;
   margin: 0 auto;
}
img {
   max-width: 100%;
   display: block;
}
ul {
   padding: 0;
   list-style: none;
   margin: 0;
}
.section {
   padding: 8rem 0;
}
